.glow-on-hover {
    width: 110px;
    height: 40px;
    border: none;
    outline: none;
    color: #fff;
    background: #111;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
    margin-left: 3px;
}

.glow-on-hover:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.glow-on-hover:active {
    color: #000
}

.glow-on-hover:active:after {
    background: transparent;
}

.glow-on-hover:hover:before {
    opacity: 1;
}

.glow-on-hover:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #111;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}

.formVideo{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20%;
}
.InputID {
    border-radius: 3px;
    border: 1px solid aliceblue; /* Add a style for the border */
    height: 35px;
    transition: 2.6s;
}

.InputID::after {
    content: ""; /* Add content for pseudo-element */
    display: block; /* Make it a block-level element to apply styles */
    transition: 3.6s;
}

/* Apply box-shadow directly to the input element */
.InputID:focus {
    /* box-shadow: 0px 1px 23px 8px #00b0ff; */
    box-shadow: 0px 1px 200px 900px #050505ae;
    border-radius: 30px;

    transition:6.16s;
}
